<template>
	<div class="index">
		<div class="g-hd">
			<div class="m-nav s-bgc-ess">
				<h1 class="s-fc-we">YUI</h1>
			</div>
		</div>
		<div class="g-bd">
			<div class="m-lst">
				<div class="f-flex f-fl-bet m-lst-mn" v-for="(list,index) in listData" :key="index"  @click="goDetails(list)">
					<div class="m-lst-tle">
						{{list}}
					</div>
					<div class="m-lst-arr">
						<i class="ydjy-arrow"></i>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				listData:[
					'',
					'',
					'/',
					'',
					'',
					'',
					'',
					'',
					'',
				]
			}
		},
		created:function(){
			
		},
		mounted:function(){
			
		},
		methods:{
			goDetails:function(data){
				var navigate;
				switch(data){
					case '':
						navigate='navbar';
						break;
					case '':
						navigate='tabbar';
						break;
					case '/':
						navigate='label';
						break;
					case '':
						navigate='button';
						break;
					case '':
						navigate='list';
						break;
					case '':
						navigate='grid';
						break;
					case '':
						navigate='form';
						break;
					case '':
						navigate='popover';
						break;
					default:
						navigate='icon';
				}
				this.$router.push({
					name:navigate
				})
			}
		}
		
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


